<template>

    <mdb-container >
      <mdb-row>
        <mdb-col style="margin-bottom: 1em;" v-for="(project, i) in projects"
            :key="i" md="4">
            <h4>{{project.title}}</h4>
          <mdb-card style="background-color: rgba(4, 24, 39, 0.85);" >
            <mdb-card-image
            class="project"
              :src="project.image"
              alt="Card image cap"
            ></mdb-card-image>
            <mdb-card-body>
              <mdb-card-title>{{project.title}}</mdb-card-title>
              <mdb-card-text>{{project.description}}</mdb-card-text>
             <a v-if="project.projectURL" :href="project.projectURL" target="_blank"><mdb-btn  color="primary">{{`Visit ${project.title}`}}</mdb-btn></a> 
            </mdb-card-body>
          </mdb-card>
        </mdb-col>
        <!-- <mdb-col col="4">col="4"</mdb-col>
        <mdb-col col="4">col="4"</mdb-col> -->
      </mdb-row>
    </mdb-container>
  
</template>


<script>
import {
  mdbContainer,
  mdbRow,
  mdbCol,
  mdbCard,
  mdbCardImage,
  mdbCardBody,
  mdbCardTitle,
  mdbCardText,
  mdbBtn
} from "mdbvue";
export default {
  name: "ProjectsContainer",
  props: ["projects"],
  components: {
    mdbContainer,
    mdbRow,
    mdbCol,
    mdbCard,
    mdbCardImage,
    mdbCardBody,
    mdbCardTitle,
    mdbCardText,
    mdbBtn
  },
  methods: {
      goToProject(projectURL) {
          this.$router.push(projectURL);
      }
  }
};
</script>

<style>
.project img {
    height: 30vh;
    margin-top: 1em;
}
</style>
